/*simple search*/
@import 'http://weloveiconfonts.com/api/?family=fontawesome';

[class*='fontawesome-']:before {
  font-family: 'fontawesome', sans-serif;
}

body {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.select-search-box {
  width: 300px;
  position: relative;
  margin-bottom: 20px;
  background: #fff;
  border-radius: 4px;
}

input[type='text'] {
  display: block;
  width: 100%;
  border: none;
  margin: 10px;
  background: none;
  outline: none;
  font-size: 16px;
  padding: 10px;
  color: #222f3e;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  z-index: 2;
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  -webkit-box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.4),
    0 3px 6px 0 rgba(0, 0, 0, 0.07);
          box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.4),
    0 3px 6px 0 rgba(0, 0, 0, 0.07);
}
.select-search-box--multiple {
  border-radius: 4px;
  -webkit-box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1),
    0 3px 6px 0 rgba(0, 0, 0, 0.07);
          box-shadow: 0 7px 14px 0 rgba(50, 50, 93, 0.1),
    0 3px 6px 0 rgba(0, 0, 0, 0.07);
  overflow: hidden;
}

.select-search-box::after {
  font-family: fontawesome;
  content: '\f078';
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  text-align: center;
  line-height: 50px;
  color: #222f3e;
  z-index: 1;
}

.select-search-box--multiple::after {
  content: '\f002';
}

.select-search-box--input::after {
  display: none !important;
}

.select-search-box__out {
  display: none;
}

.select-search-box__search {
  display: block;
  width: 100%;
  height: 50px;
  border: none;
  background: none;
  outline: none;
  font-size: 16px;
  padding: 0 20px;
  color: #222f3e;
  -webkit-appearance: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  z-index: 2;
  cursor: pointer;
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  line-height: 50px;
